header{
  background-color: darkslategray;
  padding:1rem;
}
.container{
  max-width:70%;
  margin:0 auto;
  
}
.home{
  text-decoration: none;
  color:white;
  float:left;
  padding-right: 1.5rem;
  font-size: x-large;
  font-weight: 100;
  
}
.links{
  text-decoration:none;
  color:white;
  font-size: x-large;
  font-weight: 100;
  /* float:right; */
}
.logout{
  float:right;
}

.login-container{
  max-width:70%;
  margin:0 auto;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content: center;
  height:70vh;
}


.h2-login{
  color:#BABECC;
  font-size: xx-large;
  font-weight: lighter;
}
.form input{
  border: 0;
  outline: 0;
  font-size: 16px;
  border-radius: 16px;
  padding: 16px;
  background-color:#EBECF0;
  text-shadow: 1px 1px 0 #FFF;
}

.form button{
  border:0;
  outline:0;
  width:230px;
  padding:16px;
  border-radius:16px;
  background-color: darkslategray;
  color:white;
  cursor:pointer;
}

.form button:disabled{
  opacity:50%;
  cursor:not-allowed;
}

.h3{
  color:#BABECC;
  text-align: center;
  font-size: xx-large;
  font-weight: lighter;
}

.content{
  border:0.5px solid #EBECF0;
  height:auto;
  margin-bottom: 1.5rem;
  padding: 0 1rem;
  font-style: oblique;
  font-family: auto;
  background-color: whitesmoke
}

.post-title{
  color:#BABECC;
  font-size: xx-large;
  font-weight: lighter;
  text-transform: uppercase;
}

.auths {
  border:0;
  outline:0;
  width:230px;
  padding:16px;
  border-radius:16px;
  background-color: rgb(240, 65, 65);
  color:white;
  cursor:pointer;
}

.posts{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding:0 3rem;
    border: 1px solid whitesmoke;
    background-color: whitesmoke;
    border-radius: 5px;
    border-bottom: 2px solid snow;
}

.posts-data{
  display:flex;
  color:#6f6f6f;
  flex-direction: column;
}

.posts-data p{
  font-family: auto;
  font-style: oblique;
}

.h1-posts{
  font-weight: 200;
  font-size:xx-large;
}

.h2-posts{
  font-weight: 200;
}
.links:hover{
  border-bottom: 2px solid white;
}

.posts p:hover{
  border-bottom: 2px solid white;
}

.btn a:hover{
  border-bottom: 5px solid rgb(1, 41, 41);
}

